.section1{
  padding: 5%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-image: url(/Assets/img/hero4.png);
  background-size: cover;
  background-position: center;
  height: 73vh;
  width: 90%;
  background-position:top 25% right 0px;
  text-align: center;
  justify-content: center;
}

.section1 h1{
  color: #088178;
}

.section3 .describe{
    display: flex;
    align-items: center;
    flex-direction: column;
  }

.section1 button{
  background-image: url(/Assets/img/button.png);
  background-size: cover;
  background-position: center;
  font-size: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #ffffff;
}
  .section4{
    margin: 10px 0;
    padding: 2vh 0;
    background-image: url(/Assets/img/banner/b2.jpg);
    width: 100%;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .section4 p{
    color: white;
  }
  .section4 h1{
    color: white;
  }
  .section4 h1{
    padding: 10px;
    font-family: 10px;
    color: white;
  }
  .section4 button{
    background-color: white;
    padding: 10px 20px;
    border-radius: 8px;
    border: 1px solid white;
  }
  
  .part{
    display: flex;
    justify-content: space-evenly;
    padding: 10px 2px;
    margin:0 2%;
  }
  .banner1, .banner2{
    background-image: url(/Assets/img/banner/b17.jpg);
    width: 100%;
    height: 30vh;
    background-size: cover;
    padding: 2%;
    background-position:top 25% right -0px;
  }
  .banner2{
    background-image: url(/Assets/img/banner/b10.jpg); 
  }
  .banner3 {
    background-image: url(/Assets/img/banner/b7.jpg);
   }
  .banner4{
    background-image: url(/Assets/img/banner/b4.jpg);
  }
  .banner5{
    background-image: url(/Assets/img/banner/b18.jpg);
  }
  .part button{
    background-color: #088178;
    padding: 5px 20px;
    color: white;
    border: 1px solid #088178;
  
  }
  .banne .describe{
    padding: 0 ;
  }
  .banner .describe{
    padding: 0;
  }
  .part button:hover{
    background-color: #088178;
    color: white;
    border: 1px solid #088178;
  }
  
  .banner{
    h2{
      display: none;
    }
    h5{
      color: #000000;
      font-size: 20px;
      background-color: #70776e86;
    }
    min-width: 100px;
    width: 30vw;
    height: 30vh;
    background-position:top 25% right 0px;
    background-size: cover;
  }
  @media (max-width:1528px){
    .features{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
  }
  
  @media (max-width:1100px){
    .section1{
      height: 25vh;
    }
    .banner{
      background-position:top 25% right -90px;
      margin: 0px;
    }
     nav ul {
       display: none;  
       flex-direction: column;
       justify-content: start; 
       position: fixed;
       top: 0;
       left: 10px;
       background-color: #088178;
       width: 50%;
       height: 100vh;
       padding: 0;
       margin-left: -10px;
     }
     .maincart{
      display: none;
     }
     nav ul :hover{
      color: white;
     }
     nav ul li{
      line-height: 10px;
     
     }
     .top{
      margin-top: 0px;
     }
     nav ul img{
      padding: 10px 10px; 
     }
  
     /*           */
     nav .menu{
      
      display: block; 
      margin: 10px ;
    } 
    header{
      display: flex;
      justify-content: space-between;
    }
    
    .cancel{
      display: block;
      height: 20px;
      width: 20px;
    }
  }
  
