.section123{
    padding: 5% 0;
    display: flex;
    color: white;
    flex-direction: column;
    align-items: center;
    background-color: rebeccapurple;
    background-image: url(../Assets/img/about/banner.png);
    background-size: cover;
    background-position: center;
    height: 15vh;
    width: 100%;
    background-position:top 25% right -80px;
    text-align: center;
    justify-content: center; 
  }
  .section124{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
  }

  .section124 .imgpart{
    padding: 30px 10px 0px 120px;
  }
  .section124 .descriptionpart{
    margin-top: 5vh;
    align-items: center;
  }
  // .section124 .imgpart img{
  //   max-width: 35vw;
  //   min-width: 35vw;
  // }
  .application{
    display: flex;
    justify-content: center;
    padding: 20px;
  }
  .appvideo{
    display: flex;
    justify-content: center;
  }
  .appvideo video{
    width: 45vw;
    border-radius: 20px;
  }
  
  @media(max-width:1100px) {
   .imgpart{
      display: flex;
      flex-direction: column;
      width: 100vw;
      justify-content: center;
      align-items: center;

      // padding: 30px 10px 0px 120px;
    }
    .section124 .imgpart img{
      width: 100%;
      border: 2px solid;
    
    }
  }