/* Reset some default styles */
  
  /* Apply a background color to the entire page */
  
  /* Center the form on the page */
  .pflrimg{
    
    display: flex;
    margin-top: 25px;
    justify-content: center;
    .edit{
      display: flex;
      flex-direction: column;
      align-items: center;
      p{
        margin-top: -20px;
      }
    }
  }
  .prfl{
    
    p{
      margin-left: 20px;
      font-size: 15px;
      font-style: italic;
      color: #088178;
    }
  }
  .pflrimg img{
    background-image: url(../Assets/profile-user.png);
    background-repeat: no-repeat;

    background-size: cover;
    
    width: 150px;
    height: 150px;
    border-radius: 100%;
  }
  .profile-settings-container {

    margin: 0px 30px;
    background-color: #fff;
    padding: 0px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    h2{
      padding: 1vh 2vw;
      color: #088178;
    }
  }
  
  /* Style form elements */
  .form-group {
    padding: 1vh 2vw;
    margin-bottom: 15px;
    label{
      margin-top: 10px;
    }
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input[type="text"],
  input[type="email"],
  input[type="password"] {
    width: 70%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  form button {
    background-color:  #088178;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  /* Add responsive styles */
  @media (max-width: 768px) {
    .profile-settings-container {
      max-width: 100%;
      padding: 10px;
    }
  }
  